body {
  background-image: url(../src/images/theatre.jpg);
  background-size: cover;
 
  
}

body h1 {
  color: white;
}

body button {
  cursor: pointer;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: green;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html {
  height: 100%;
  width: 100%;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;

}

.wrapper {
  padding: 5px;
  max-width: 960px;
  width: 95%;
  margin: 20px auto;

}

header {
  padding: 10px 10px;
  text-align: center;
}

.columns {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin: 5px;
 text-align: center;
}

.column {
  flex: 1;
  border: 1px solid blue;
  margin: 5px;
  padding: 5px;
  background-color: rgba(235, 227, 227, 0.9);

}

.column:first-child {
  margin-left: 0;
}

.column:last-child {
  margin-right: 0;
}

footer {
  padding: 10px;
  margin: 5px;
  background-color: #f0f0f0;
  border: 1px solid red;
  text-align: center;

}

.preview {
  position: sticky;
 top: 1px;
 
}


.preview p {
 margin: 10px;
 font-size: 20px;
}

@media screen and (max-width: 980px){
  .columns .column {
      margin-bottom: 5px;
      flex-basis: 40%;
  }
  .columns .column:nth-last-child(2) {
      margin-right: 0;
  }
  .columns .column:last-child {
      flex-basis: 100%;
      margin: 0;
  }
}

@media screen and (max-width: 680px) {
  .columns .column {
      flex-basis: 100%;
      margin: 0 0 5px 0;
  }
}
